.container {
  background-image: linear-gradient(180deg, #7920c7, #55055f);
  height: 100vh;
  color: white;
  min-height: 600px;
  display: grid;
  grid-template-rows: repeat(2, auto);
  margin-top: -70px;
}

.greeting {
  font-size: 70px !important;
}

.greetingtext {
  padding-top: 50px;
  font-size: 25px;
  line-height: 150%;
}
.bgwave {
  position: absolute;
  z-index: 0;
  flex: none;
  bottom: 0;
  opacity: 0.2;
  mix-blend-mode: hard-light;
  width: 100%;
  height: auto;
}
.waves {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.wave {
  opacity: 0.8;
  z-index: 5;
  flex: none;
  bottom: 0;
  background-image: url("../../assets/waveblue.png");
  width: 100%;
  height: 100%;
  background-repeat: repeat-x;
  background-size: auto 100%;
  /*adjust s value for speed*/
  animation: animatedBackground 400s linear infinite;
  margin-left: -100%;
}

.wave2 {
  opacity: 0.8;
  flex: none;
  bottom: 0;
  background-image: url("../../assets/waveorange.png");
  width: 100%;
  height: 100%;
  background-repeat: repeat-x;
  background-size: auto 100%;
  /*adjust s value for speed*/
  animation: animatedBackground2 600s linear infinite;
  z-index: 3;
}

.yellowicons {
  z-index: 2;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin-left: -100%;
  align-items: flex-end;
  color: #fdb14d;
  font-size: 50px;
}

.blueicons {
  z-index: 4;
  color: #55aaff;
  width: 100%;
  margin-left: -100%;
  display: flex;
  justify-content: space-evenly;
  font-size: 50px;
  align-items: flex-end;
}

.icon1 {
  animation: jump 8s infinite;
}
.icon2 {
  animation: jump 8s infinite;
  animation-delay: 2s;
}
.icon3 {
  animation: jump 8s infinite;
  animation-delay: 4s;
}
.icon4 {
  animation: jump 8s infinite;
  animation-delay: 6s;
}
.icon5 {
  animation: jump 8s infinite;
  animation-delay: 8s;
}
.icon6 {
  animation: jump 8s infinite;
  animation-delay: 10s;
}
.icon7 {
  animation: jump 8s infinite;
  animation-delay: 12s;
}
.icon8 {
  animation: jump 8s infinite;
  animation-delay: 16s;
}
.icon9 {
  animation: jump 8s infinite;
  animation-delay: 18s;
}
.icon10 {
  animation: jump 8s infinite;
  animation-delay: 9s;
}
.icon11 {
  animation: jump 8s infinite;
  animation-delay: 11s;
}
.icon12 {
  animation: jump 8s infinite;
  animation-delay: 13s;
}
@keyframes jump {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-600%);
  }
  100% {
    transform: translateY(0%);
  }
}

@keyframes animatedBackground {
  from {
    background-position: 0 0;
  }
  /*use negative width if you want it to flow right to left else and positive for left to right*/
  to {
    background-position: -10000px 0;
  }
}

@keyframes animatedBackground2 {
  from {
    background-position: 0 0;
  }
  /*use negative width if you want it to flow right to left else and positive for left to right*/
  to {
    background-position: 10000px 0;
  }
}

@media screen and (max-width: 1600px) {
  .yellowicons,
  .blueicons {
    font-size: 35px;
  }
}

@media screen and (max-width: 1400px) {
  h1.greeting {
    font-size: 60px !important;
  }
  .yellowicons,
  .blueicons {
    font-size: 25px;
  }
}

@media screen and (max-width: 800px) {
  h1.greeting {
    font-size: 40px !important;
  }
  .yellowicons,
  .blueicons {
    font-size: 20px;
  }
}

@media screen and (max-width: 520px) {
  h1.greeting {
    font-size: 35px !important;
  }

  .greetingtext {
    font-size: 15px;
  }
}

@media screen and (max-width: 400px) {
  h1.greeting {
    font-size: 30px !important;
  }
  .greetingtext {
    font-size: 15px;
  }
}
