.upicon {
  padding: 3px;
}

.upiconbg {
  color: #7920c7;
  background-color: white;
  width: 25px;
  height: 25px;
  border-radius: 50px;
  display: flex;
  flex-direction: row;
  overflow: hidden;
  right: 30px;
  bottom: 30px;
  cursor: pointer;
  position: fixed;
  opacity: 0.5;
  -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 1s; /* Firefox < 16 */
  -ms-animation: fadein 1s; /* Internet Explorer */
  -o-animation: fadein 1s; /* Opera < 12.1 */
  animation: fadein 1s;
}

.upiconbg:hover {
  margin-right: 0px;
  opacity: 1;
  max-width: 114px;
  animation: expand 2s ease-in-out;
}

@keyframes expand {
  from {
    opacity: 0.75;
  }
  to {
    opacity: 1;
    width: 100%;
  }
}

/* Firefox < 16 */
@-moz-keyframes expand {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
    width: auto;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes expand {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
    width: auto;
  }
}

/* Internet Explorer */
@-ms-keyframes expand {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
    width: auto;
  }
}

/* Opera < 12.1 */
@-o-keyframes expand {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
    width: auto;
  }
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.5;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.5;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
  }
  to {
    opacity: 0.5;
  }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from {
  }
  to {
    opacity: 0.5;
  }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
  from {
  }
  to {
    opacity: 0.5;
  }
}

