.container-footer {
  display: flex;
  background-image: linear-gradient(180deg, #303030, #383838);
  height: 200px;
  justify-content: center;
}

.inner-container {
  margin-top: 50px;
  color: white;
  display: grid;
  grid-template-rows: 70px 30px;
  grid-gap: 10px;
  justify-items: center;
}

.email {
  font-size: 15px;
  color: white;
}

.email:hover {
  text-decoration: underline;
}
