.Slide-root-20 {
  height: 100%;
  background-color: black !important;
}

.unit {
  width: 100%;
  height: auto;
  background-color: rgba(0, 0, 0, 0.259);
  display: grid;
  grid-template-columns: repeat(2, auto);
  border-radius: 10px;
}

.unit-image {
  display: flex;
  float: left;
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.unit-textbox {
  width: 300px;
  margin-top: auto;
  margin-bottom: auto;
  color: white;
  margin-left: 15px;
  margin-right: 15px;
  justify-self: center;
}

.unit-title {
  margin-bottom: 20px;
  font-size: 30px;
  font-weight: bold;
}

@media screen and (max-width: 1300px) {
  .unit-textbox {
    width: 250px;
  }
  .unit-title {
    font-size: 20px;
  }
  .unit-textbox p {
    font-size: 15px;
  }
}
@media screen and (max-width: 1200px) {
  .unit-textbox {
    width: 200px;
  }
  .unit-title {
    font-size: 18px;
  }
  .unit-textbox p {
    font-size: 11px;
  }
}
@media screen and (max-width: 950px) {
  .unit-textbox {
    width: 150px;
  }
}
@media screen and (max-width: 800px) {
  .unit {
    grid-template-columns: 100%;
    grid-template-rows: repeat(2, auto);
  }
  .unit-textbox {
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 500px) {
  .unit-text {
    font-size: 11px;
  }

  .unit-title {
    font-size: 15px;
  }
}
